@import '../../misc/mixin.scss';
@import '../../misc/variables.scss';

.landing {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: -120px;
  background-size: 100%;
  overflow-x: hidden;

  section {
    margin-bottom: 150px;
    display: block;
    padding: 0 calc((100% - #{$main-content-width}) / 2);
    width: 100%;

    @include for-specific-size($adaptive-start) {
      padding: 0 30px;
      margin-bottom: 140px;
    }

    @include for-specific-size($main-content-width) {
      margin-bottom: 120px;
    }

    @include for-specific-size($mobile-start) {
      margin-bottom: 80px;
    }

    @include for-specific-size($mobile-min) {
      margin-bottom: 40px;
    }
  }

  &__main {
    margin-top: 0 !important;
    padding: 0 !important;
    position: relative;

    &:before {
      position: absolute;
      width: 100%;
      height: 100%;
      content: '';
      background-size: 100% 100%;
      background: url('../../media/img/main-screen-bg.png') no-repeat;
    }

    &__carousel {
      margin-top: 200px;

      &__dot, &__dot_selected {
        cursor: pointer;
        display: inline-block;
        margin-left: 8px;
        margin-right: 8px;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        z-index: -1 !important;
      }

      &__dot {
        background: #1d2127
      }

      &__dot_selected {
        background: var(--main-design-color)
      }

      &__slide {
        align-items: center;
        margin-bottom: 10vh;
        margin-left: auto;
        margin-right: auto;
        width: 1230px;
        display: flex;
        padding: 25px;

        @include for-specific-size($main-content-width) {
          width: 100%;
        }

        @include for-specific-size($mobile-start) {
          flex-direction: column;
          justify-content: center;
        }

        &__text, img {
          flex: 1 1 0;
          width: 50% !important;
          display: inline-block;

          @include for-specific-size($mobile-start) {
            width: 100% !important;
          }
        }

        &__text {
          text-align: left;
          font-size: 28px;
          padding-right: 25px;
          color: white;

          h3, h4 {
            font-weight: 400 !important;
            margin-top: 25px;
          }

          span, h4 {
            color: var(--main-design-color);
          }

          h3 {
            font-size: .75em;
          }

          h4 {
            font-size: .55em;
            margin-bottom: 34px;
          }

          h2 {
            @include for-specific-size($main-content-width) {
              font-size: 1.2em;
            }
          }

          h3 {
            @include for-specific-size($main-content-width) {
              font-size: .6em;
            }
          }

          h4 {
            @include for-specific-size($main-content-width) {
              font-size: .45em;
            }
          }
        }
      }
    }
  }

  &__last_transactions {
    font-size: 20px;
    background: var(--main-bg-color);
    padding: 30px 0 20px 0;
    color: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;

    b {
      border-bottom: 1px solid #282b33;
      width: 100%;
      padding-left: 25px;
      padding-bottom: 15px;
      font-weight: 400;
    }

    a {
      margin-top: 24px;
      color: var(--main-design-color);
      font-size: .8em;
      text-decoration: none;
      display: block;
      font-weight: 500;

    }

    table {
      width: 100%;
      border-collapse: collapse;
      position: relative;

      &:before {
        filter: opacity(.8);
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: linear-gradient(180deg, rgba(17, 19, 23, 0) 0, rgba(17, 19, 23, 0) 20%, #111317);;
      }

      td {
        padding: 15px 25px;
        border-bottom: 1px solid #282b33;

        div {
          display: flex;
          flex-direction: column;
          font-size: 0.9em;

          small {
            margin-bottom: 5px;
            color: var(--light-text-color);
          }

          p {
            display: flex;
            align-items: center;

            img, svg {
              margin-right: 8px;
            }

            img {
              height: 1.3em;
            }
          }
        }

        @include for-specific-size($mobile-start) {
          flex: 0 0 50%;

          &:nth-child(2), &:first-child {
            border-bottom: none;
          }
        }


        @include for-specific-size($mobile-min) {
          flex: 100%;
          border-bottom: none;
        }
      }

      tr {
        @include for-specific-size($mobile-start) {
          display: flex;
          flex-wrap: wrap;
        }

        @include for-specific-size($mobile-min) {
          flex-wrap: nowrap;
          flex-direction: column;

          &:not(:first-child) {
            display: none;
          }
        }
      }
    }
  }

  &__easy_start {
    font-size: 28px !important;
    position: relative;

    h2 {
      margin-bottom: 80px;
      color: white;
      text-align: center;

      @include for-specific-size($mobile-min) {
        text-align: left;
        margin-bottom: 40px;
        font-size: .8em;
      }

      span {
        color: var(--main-design-color)
      }
    }

    &__decoration_text {
      z-index: -1;
      font-weight: 700;
      opacity: .35;
      position: absolute;
      left: 0;
      top: 0;
      font-size: 10em;
      transform: translate(0, 0);
      background: -webkit-linear-gradient(180deg, hsla(0, 0%, 100%, .077), hsla(0, 0%, 100%, 0) 286.21%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    &__tiles {
      display: flex;
      justify-content: space-between;
      column-gap: 30px;
      margin-bottom: 80px;

      @include for-specific-size($mobile-start) {
        flex-direction: column;
        row-gap: 20px;
        margin-bottom: 60px;
      }

      @include for-specific-size($mobile-min) {
        margin-bottom: 15px;
      }

      &__tile {
        display: flex;
        align-items: center;
        padding: 26px 20px 26px 40px;
        flex: 1 1 0;
        background: url(data:image/webp;base64,) no-repeat;
        background-size: cover;
        border-radius: 8px;

        @include for-specific-size($mobile-start) {
          padding: 20px 14px 20px 16px;
        }

        svg {
          opacity: .55;
          height: 55px;
          width: 55px;
          flex: 0 0 55px;
          margin-right: 20px;

          @include for-specific-size($mobile-start) {
            height: 38px;
            width: 38px;
            flex: 0 0 38px;
          }
        }

        span {
          color: white;
          font-weight: 500;
          font-size: .7em;

          @include for-specific-size($mobile-start) {
            font-size: .6em;
          }
        }
      }
    }
  }

  &__about_us {
    font-size: 28px !important;
    position: relative;

    h2 {
      margin-bottom: 80px;
      color: white;
      text-align: center;

      @include for-specific-size($mobile-min) {
        text-align: left;
        margin-bottom: 40px;
        font-size: .8em;
      }

      span {
        color: var(--main-design-color)
      }
    }

    &__main_description {
      display: flex;
      align-items: center;
      margin-bottom: 50px;

      @include for-specific-size(780px) {
        flex-direction: column;
      }

      p {
        color: white;
        font-weight: 300;
        font-size: 0.6em;
        display: block;
        flex: 0 0 60%;
        padding-right: 10%;

        @include for-specific-size($mobile-start) {
          padding-right: 3%;
          font-size: 0.55em;
          flex: 0 0 70%;
        }
      }

      img {
        flex: 0 0 30%;
        display: block;
        object-fit: scale-down;

        @include for-specific-size($mobile-start) {
          width: 30%;
        }

        @include for-specific-size(780px) {
          margin-top: 30px;
          width: 60%;
        }
      }
    }

    &__decoration {
      z-index: -1;
      font-weight: 700;
      opacity: .35;
      position: absolute;
      left: 0;
      right: 0;
      text-align: right;
      font-size: 10em;
      transform: translate(0, 0);
      background: -webkit-linear-gradient(180deg, hsla(0, 0%, 100%, .077), hsla(0, 0%, 100%, 0) 286.21%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    &__tiles {
      display: flex;
      justify-content: space-between;
      column-gap: 30px;

      @include for-specific-size($mobile-start) {
        flex-direction: column;
        row-gap: 40px;
      }

      @include for-specific-size($mobile-min) {
        row-gap: 20px;
        margin-bottom: 40px;
      }

      &__tile {
        padding: 20px 20px 30px 20px;
        flex: 1 1 0;
        background: url(data:image/webp;base64,) no-repeat;
        background-size: cover;
        border-radius: 8px;
        color: white;
        position: relative;

        @include for-specific-size($mobile-start) {
          padding: 15px 15px 25px 15px;
        }

        b {
          display: block;
          font-size: .7em;
          margin-bottom: 12px;

          @include for-specific-size($mobile-min) {
            font-size: .6em;
            margin-bottom: 10px;
          }
        }

        p {
          font-size: .55em;

          @include for-specific-size($mobile-min) {
            font-size: .45em;
          }
        }

        &:before {
          position: absolute;
          content: attr(data-sub-text);
          right: 5%;
          bottom: 0;
          font-size: 3em;
          line-height: .8em;
          font-weight: bold;
          background: -webkit-linear-gradient(180deg, hsla(0, 0%, 100%, .17), hsla(0, 0%, 100%, 0));
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;

          @include for-specific-size($mobile-min) {
            font-size: 2em;
            right: 2%;
          }
        }
      }
    }
  }

  &__build_team {
    display: block;
    font-size: 28px !important;
    position: relative;

    h2 {
      margin-bottom: 80px;
      color: white;
      text-align: center;

      @include for-specific-size($mobile-min) {
        text-align: left;
        margin-bottom: 40px;
        font-size: .8em;
      }

      span {
        color: var(--main-design-color)
      }
    }

    &__referral_description {
      margin-bottom: 40px;
      padding: 25px 20px;
      background: var(--main-bg-color);
      color: white;
      border-radius: 8px;

      b {
        display: block;
        font-size: .7em;
        margin-bottom: 10px;
      }

      small {
        color: var(--light-text-color);
        display: block;
        font-size: .5em;
        margin-bottom: 30px;
      }

      &__tiles {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;

        @include for-specific-size($mobile-start) {
          padding-bottom: 40px;
        }

        &:before, &:after {
          content: '';
          position: absolute;
          top: 50%;
          transform: translate(0, -50%);
          left: 0;
          width: 100%;
        }

        &:before {
          background-color: var(--additional-bg-color);
          height: 6px;

          @include for-specific-size($mobile-start) {
            top: calc(50% - 20px)
          }
        }

        &:after {
          height: 24px;
          background-image: url('../../media/img/arrow-left.svg'), url('../../media/img/arrow-left.svg'), url('../../media/img/arrow-left.svg'), url('../../media/img/arrow-left.svg');
          background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
          background-size: 22px;
          background-position: 16% center, 39% center, 61% center, 84% center;

          @include for-specific-size($mobile-start) {
            background: none;
          }
        }

        &__tile {
          background-size: cover;
          background: url("data:image/webp;base64, ") no-repeat;
          background-size: 100%;
          border-radius: 50%;
          width: 120px;
          height: 120px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          z-index: 1;

          @include for-specific-size($mobile-start) {
            width: 60px;
            height: 60px;
            position: relative;
          }

          span {
            font-size: .9em;
            color: var(--main-design-color);
            display: block;
            margin-bottom: 7px;

            @include for-specific-size($mobile-start) {
              margin-bottom: 0;
              font-size: .65em;
            }
          }

          p {
            font-size: .6em;

            @include for-specific-size($mobile-start) {
              position: absolute;
              top: 100%;
              margin-top: 10px;
              font-size: .5em;
              left: 0;
              width: 100%;
              text-align: center;
            }

            br {
              display: none;

              @include for-specific-size($mobile-start) {
                display: block;
              }
            }


          }
        }
      }
    }
  }

  &__contributors {
    font-size: 28px !important;
    position: relative;

    h2 {
      margin-bottom: 80px;
      color: white;
      text-align: center;

      @include for-specific-size(760px) {
        margin-bottom: 40px;
      }

      span {
        color: var(--main-design-color)
      }
    }

    &__decoration {
      z-index: -1;
      font-weight: 700;
      opacity: .35;
      position: absolute;
      text-align: center;
      left: 50%;
      font-size: 10em;
      transform: translate(-50%, 0);
      background: -webkit-linear-gradient(180deg, hsla(0, 0%, 100%, .077), hsla(0, 0%, 100%, 0) 286.21%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    &__logos {
      display: flex;
      width: 100%;
      padding: 30px 80px;
      align-self: center;
      justify-content: space-between;
      margin-bottom: 80px;

      @include for-specific-size(760px) {
        padding: 15px 40px;
        flex-direction: column;
        row-gap: 30px;
      }

      img {
        height: 100px;
        object-fit: scale-down;

      }
    }

    &__banner {
      padding: 30px 40px 30px 200px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: var(--main-bg-color) url('data:image/png;base64,');
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 8px;
      font-size: 0.7em;

      @include for-specific-size(830px) {
        padding: 30px 40px 30px 150px;
        background-position: -50px 0px;
      }

      @include for-specific-size(760px) {
        margin-bottom: 40px;
        padding: 30px;
        flex-direction: column;

      }

      @include for-specific-size($mobile-min) {
        background: var(--main-bg-color);
      }

      span {
        display: block;

        @include for-specific-size($mobile-min) {
          display: inline;
        }
      }

      h2 {
        text-align: left;
        margin: 0;

        @include for-specific-size(760px) {
          text-align: center;
          font-size: 1.2em;
          display: flex;
          align-items: center;
          column-gap: 8px;
          margin-bottom: 20px;
        }

        @include for-specific-size($mobile-min) {
          display: inline;
        }

      }
    }
  }
}