@import '../../misc/mixin.scss';
@import '../../misc/variables.scss';

.leader_program {
  background: var(--main-bg-color);
  padding: 30px 24px;
  border-radius: 8px;
  font-size: 20px;

  &__header {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;

    @include for-specific-size($mobile-start) {
      flex-direction: column;
    }


    b {
      display: block;
      margin-bottom: 10px;
    }

    small {
      font-weight: 300;
      display: inline-block;
      font-size: .7em;
      line-height: 1.3em;
      color: var(--light-text-color);
    }

    &__stats {
      display: flex;
      align-items: center;
      column-gap: 14px;
      margin-left: 20px;
      font-size: .9em;
      align-self: flex-end;

      @include for-specific-size($mobile-start) {
        align-self: flex-start;
        margin-left: 0;
        margin-top: 20px;
      }


      div {
        background: var(--additional-bg-color);
        padding: 8px 12px;
        border-radius: 8px;

        p {
          white-space: nowrap;

          @include for-specific-size($mobile-start) {
            font-size: .8em;
          }

          span {
            margin-left: 4px;
            color: var(--main-design-color)
          }
        }
      }
    }
  }

  &__body {
    margin-top: 14px;
    border-collapse: collapse;
    font-size: .7em;
    width: 100%;

    &[data-from-dashboard=true] {
      @include for-specific-size(1600px) {
        display: none;
      }
    }

    &[data-from-dashboard=false] {
      @include for-specific-size($mobile-start) {
        display: none;
      }
    }



    br {
      display: block;
    }

    tr:first-child {
      td {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }
    }

    tr:last-child {
      td {
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }
    }

    th {
      font-weight: 400;
      text-align: left;
      color: var(--light-text-color);
    }

    td[colspan] {
      padding: 0;
    }

    td {
      padding: 8px 0 16px 0;
      text-align: center;
      color: white;
      font-size: 1.2em;

      &:nth-child(2) {
        span {
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
        }
      }


      &:last-child {
        span {
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }
      }

      &[data-current-level = true] {
        background: var(--additional-bg-color);

        span {
          &[data-completed-level=true] {
            &:after {
              width: 50%;
            }
          }
        }
      }

      svg {
        width: 1.5em;
        height: 1.5em;
      }
    }

    span {
      font-weight: 800;
      color: var(--main-design-color)
    }

    &__progress_bar {
      vertical-align: middle;

      span {
        height: 6px;
        display: block;
        background: var(--additional-bg-color);
        transform: translate(0, 75%);

        &:after {
          position: absolute;
          content: '';
          height: 100%;
          width: 100%;
          left: 0;
          top: 0;
          background: transparent;
        }

        &[data-completed-level=true] {
          &:after {
            background: var(--main-design-color);
          }
        }
      }
    }
  }

  &__body_mobile {
    margin-top: 14px;
    font-size: .7em;
    width: 100%;
    flex-direction: column;
    row-gap: 15px;
    display: none;

    &[data-from-dashboard=true] {
      @include for-specific-size(1600px) {
        display: flex;
      }
    }

    &[data-from-dashboard=false] {
      @include for-specific-size($mobile-start) {
        display: flex;
      }
    }

    &__tile {
      color: white;
      font-size: 1.2em;
      background: var(--light-bg-color);
      border-radius: 8px;

      &[data-completed-level=true] {
        background: url('data:image/webp;base64,') no-repeat;
        background-size: cover;
      }

      span {
        color: var(--main-design-color)
      }

      span, i {
        font-weight: 900;
        font-style: normal;
      }

      &__header, &__body {
        padding: 15px 12px 15px 12px;
        display: flex;
      }

      &__header {
        width: 100%;
        border-bottom: 1px solid rgba(#909090, .15);
        align-items: center;
        justify-content: space-between;
      }

      &__body {
        font-size: .9em;
        flex-direction: column;
        row-gap: 15px;

        p {
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
      }
    }
  }
}